import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import AuthForm from "../../componenets/auth/AuthForm";
import { check } from "../../modules/user";
import {
  changeErrorText,
  changeField,
  initializeForm,
  register,
} from "./../../modules/auth";

const RegisterForm = () => {
  const dispatch = useDispatch();
  const { form, auth, authError, user, errorText } = useSelector(
    ({ auth, user }) => ({
      form: auth.register,
      auth: auth.auth,
      authError: auth.authError,
      user: user.user,
      errorText: auth.errorText,
    })
  );

  // 인풋 변경 핸들러
  const onChange = (e) => {
    const { value, name } = e.target;
    dispatch(changeField({ form: "register", key: name, value }));
  };

  // 폼 등록 핸들러
  const onSubmit = (e) => {
    e.preventDefault();

    const { username, password, passwordConfirm } = form;
    if ([username, password, passwordConfirm].includes("")) {
      dispatch(changeErrorText("빈 칸을 모두 입력하세요."));

      return;
    }

    if (password !== passwordConfirm) {
      dispatch(changeErrorText("비밀번호가 일치하지 않습니다."));

      dispatch(changeField({ form: "register", key: "password", value: "" }));
      dispatch(
        changeField({ form: "register", key: "passwordConfirm", value: "" })
      );
      return;
    }

    dispatch(register({ username, password }));
  };

  // 컴포넌트가 처음 렌더링될 때 form을 초기화함.
  useEffect(() => {
    dispatch(initializeForm("register"));
  }, [dispatch]);

  // 회원가입 성공/실패
  useEffect(() => {
    if (authError) {
      console.log(authError);

      if (authError.response.status === 409) {
        dispatch(changeErrorText("이미 존재하는 계정입니다."));
        return;
      }
    }
    if (auth) {
      console.log("회원가입 성공");
      console.log(auth);
      dispatch(check());
    }
  }, [auth, authError, dispatch]);

  // user값 설정 확인
  useEffect(() => {
    if (user) {
      console.log("check API success");
      console.log(user);
    }
  }, [user]);

  const navigate = useNavigate();

  // 회원가입되면 홈으로 이동
  useEffect(() => {
    if (user) {
      navigate("/");
    }
    try {
      localStorage.setItem("user", JSON.stringify(user));
    } catch (e) {
      console.log("localStorage is not working");
    }
  }, [user, navigate]);

  // 처음 페이지 mount됬을 때 errortext init
  useEffect(() => {
    dispatch(changeErrorText(""));
  }, []);
  return (
    <AuthForm
      type="register"
      form={form}
      onChange={onChange}
      onSubmit={onSubmit}
      errorText={errorText}
    />
  );
};

export default RegisterForm;
